.button {
  text-align: center;
  font-size: 23px;
  font-weight: 800;
  text-transform: uppercase;
  background-color: transparent;
  border: 3px solid var(--txt-black);
  padding: 10px 30px;
}
.button__new {
  padding-top: -25px;
}
@media screen and (max-width: 1280px) {
  .button {
    font-size: 18px;

    padding: 10px 20px;
  }
}
