.bestProduct__card {
  position: relative;
  width: 33%;
}
.bestProduct__card-number {
  position: absolute;
  top: -8%;
}
.bestProduct__card-number span {
  position: absolute;
  top: 30%;
  left: 30%;
  color: var(--txt-black);
  font-size: 30px;
  font-weight: 800;
  border-bottom: 2px solid var(--txt-black);
}
.bestProduct__card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.bestProduct__card-content {
  background-color: var(--white);
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 5px 5px 30px;
  border: 4px solid var(--txt-black);
  position: absolute;
  top: 90%;
  left: 10%;
}
.bestProduct__card-content p,
.bestProduct__card-content button {
  color: var(--txt-black);
  font-weight: 800;
}
.bestProduct__card-content p {
  width: 60%;
}
.bestProduct__card-content button {
  font-size: 20px;
  width: 65px;
  height: 65px;
  color: var(--white);
  text-transform: uppercase;
  background-color: var(--txt-black);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 1800px) {
  .bestProduct__card-content p {
    font-size: 18px;
  }
}
@media screen and (max-width: 1600px) {
  .bestProduct__card-number {
    top: -10%;
  }
  .bestProduct__card-content p {
    width: 70%;
  }
  .bestProduct__card-content button {
    font-size: 18px;
    width: 60px;
    height: 60px;
  }
}
@media screen and (max-width: 1360px) {
  .bestProduct__card-number {
    top: -12%;
  }
  .bestProduct__card-number span {
    font-size: 24px;
  }
  .bestProduct__card-number img {
    width: 90%;
  }
  .bestProduct__card-content {
    padding: 5px;
  }
  .bestProduct__card-content p {
    font-size: 14px;
    width: 70%;
    line-height: 25px;
  }
  .bestProduct__card-content button {
    font-size: 14px;
    width: 45px;
    height: 45px;
  }
  .bestProduct__card-content {
    width: 90%;
    left: 5%;
  }
}
@media screen and (max-width: 960px) {
  .bestProduct__card-number {
    top: -18%;
  }
  .bestProduct__card-number span {
    font-size: 18px;
  }
  .bestProduct__card-number img {
    width: 90%;
  }
  .bestProduct__card-content {
    padding: 5px;
  }
  .bestProduct__card-content p {
    font-size: 12px;
    line-height: 20px;
  }
  .bestProduct__card-content button {
    font-size: 12px;
    width: 40px;
    height: 40px;
  }
}
