:root {
  --black-two: #111111;
  --white: #ffffff;
  --txt-black: #1a1a1a;
  --txt-orange: #f96400;
  --bn-bg: #ea4917;
  --mid-blue: #245fc5;
  --sun-yellow: #fcd32a;
  --cerulean: #0565bb;
  --bright-orange: #ff5500;
  --white-three: #f4f4f4;
  --light-grey: #ced1c8;
  --white-blur: rgba(255, 255, 255, 0.5);
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: unset;
}

p {
  font-size: 20px;
  line-height: 30px;
  color: var(--txt-black);
}

button {
  cursor: pointer;
  border: 0;
  outline: none;
  /* color: var(--txt-black); */
}
button:active {
  transform: scale(0.98);
}
ul {
  list-style-type: none;
  color: var(--white);
  font-size: 14px;
  text-transform: uppercase;
}
