.title {
  padding-top: 150px;
  width: 690px;
  height: 100%;
  margin: 0 auto;
  text-align: center;
}

.title h5 {
  font-size: 50px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
}
.title p {
  padding: 30px 0;
  padding-bottom: 27px;
  line-height: 30px;
}
.title__bestProduct h5,
.title__bestProduct p {
  color: var(--txt-orange);
}
.title__brandStory h5,
.title__brandStory p {
  color: var(--sun-yellow);
}
.title__brandStory p {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: lighter;
  font-size: 16px;
}

.title__new h5 {
  font-weight: bold;
  font-size: 45px;
}
.title__new p {
  font-size: 16px;
  font-weight: 400;
}
@media screen and (max-width: 1360px) {
  .title {
    padding-top: 100px;
  }
  .title h5 {
    font-size: 40px;
  }
}
@media screen and (max-width: 960px) {
  .title {
    padding-top: 70px;
    width: 500px;
  }
  .title h5 {
    font-size: 30px;
  }
  .title p {
    font-size: 14px;
    padding: 10;
  }
}
