.brandStory__contaier {
  width: 100%;
  height: 100%;
  /* background-image: url('../../../assets/bg/brandStory.png'); */
}
.brandStory__table {
  width: 1250px;
  margin: 0px auto;
  padding-top: 30px;
}

.table__top {
  width: 100%;
  height: 23px;
  background-color: var(--black-two);
  padding: 9px 9px 6px 9px;
}
.table__crossbar {
  width: 100%;
  height: 100%;
  border-top: 2px solid var(--cerulean);
  border-bottom: 2px solid var(--cerulean);
}
.table__content {
  display: flex;
  width: 100%;
  height: 420px;
}
.table__content-left {
  width: 50%;
  height: 100%;
  border: 1px solid var(--black-two);
}
.table__content-left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.table__content-right {
  width: 50%;
  height: 100%;
  border: 3px solid var(--black-two);
  padding: 60px 0 50px 45px;
  background-color: var(--white);
}
.table__content-right h6 {
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
}
.table__content-right p {
  font-size: 16px;
  font-weight: lighter;
  width: 90%;
  font-family: 'Noto Sans KR', sans-serif;
  padding: 32px 0;
}
@media screen and (max-width: 1360px) {
  .brandStory__table {
    width: 960px;
  }
  .table__content-right {
    padding: 30px 0 30px 20px;
  }
  .table__content-right h6 {
    font-size: 20px;
  }
  .table__content-right p {
    font-size: 16px;
  }
}
@media screen and (max-width: 960px) {
  .brandStory__table {
    width: 750px;
  }
  .table__content-right {
    padding: 20px 0 20px 10px;
  }
  .table__content-right h6 {
    font-size: 16px;
  }
  .table__content-right p {
    font-size: 14px;
  }
}
