.footer__container {
  height: 100%;
}
.vdHome__content {
  width: 1250px;
  margin: 0 auto;
  margin-top: 70px;
  margin-bottom: 145px;
}
.vdHome__content-toolbar {
  padding: 10px 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--black-two);
}
.vdHome__toolbar-whp {
  display: flex;
  justify-content: center;
  align-self: center;
}
.vdHome__toolbar-whp p {
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
  color: var(--white);
}
.vdHome__toolbar-crossbar {
  flex: 1;
  border-top: 3px solid var(--white);
  border-bottom: 3px solid var(--white);
  padding: 4px 0;
  margin: 0 35px 0 20px;
}

.vd__content-url {
  border-left: 4px solid var(--black-two);
  border-right: 4px solid var(--black-two);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.vd__content-url p {
  font-size: 18px;
  font-weight: 800;
  text-transform: uppercase;
  padding: 5px 20px;
}
.vd__content-url p:last-child {
  font-size: 25px;
  -webkit-text-stroke: 2px var(--black-two);
}
.vd__content-bg {
  width: 100%;
  height: 100%;
  position: relative;
}
.vd__content-bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.vd__content-title {
  width: 80%;
  border: 2px solid var(--black-two);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 83%;
  left: 10%;
  background-color: var(--white);
}
.vd__content-title2 {
  height: 100%;
  background-color: var(--sun-yellow);
}
.vd__content-title p {
  font-size: 25px;
  font-weight: bold;
  padding: 5px 20px;
}
.vd__content-title1 {
  width: 90%;
  display: flex;
  justify-content: center;
}
.vd__content-title1 span {
  font-weight: 500;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 26px;
}
@media screen and (max-width: 1360px) {
  .vdHome__content {
    width: 900px;
  }
  .vdHome__toolbar-whp p,
  .vd__content-title p,
  .vd__content-title1 span {
    font-size: 20px;
  }
  .vd__content-url p {
    font-size: 16px;
  }
}
@media screen and (max-width: 960px) {
  .vdHome__content {
    width: 750px;
  }
  .vdHome__toolbar-whp p,
  .vd__content-title p,
  .vd__content-title1 span {
    font-size: 18px;
  }
  .vd__content-url p {
    font-size: 14px;
  }
}
