.propducts__container {
  padding: 140px 0px;
  width: 1570px;
  margin: 0 auto;
  position: relative;
}
.cards__container {
  width: 100%;
  height: 2220px;
  overflow: hidden;
}
.cards {
  display: flex;
  flex-wrap: wrap;
}
.cards__shadow {
  width: 100%;
  height: 100px;
  position: absolute;
  background-color: var(--white);
  box-shadow: 0px -50px 60px var(--white);
  top: 90%;
  left: 0;
  z-index: 0;
  display: none;
}
.products__button {
  width: 100%;
  text-align: center;
  position: relative;
}
@media screen and (max-width: 1800px) {
}
@media screen and (max-width: 1600px) {
  .propducts__container {
    width: 1250px;
    padding: 120px 0;
  }
  .cards__container {
    height: 1780px;
  }
  .products__button {
    padding-top: 70px;
  }
  .cards__shadow {
    display: block;
  }
}
@media screen and (max-width: 1280px) {
  .propducts__container {
    width: 940px;
    padding: 120px 0;
  }
  .cards__container {
    height: 1850px;
  }
}
@media screen and (max-width: 960px) {
  .propducts__container {
    width: 750px;
    padding: 120px 0;
  }
  .cards__container {
    height: 1850px;
  }
}
