.footer__container {
  border-top: 1px solid var(--white-three);
  border-bottom: 1px solid var(--white-three);
  margin-left: 101px;
  padding-top: 50px;
  padding-bottom: 60px;
}
.footer__content {
  display: flex;
  justify-content: space-between;
}

.footer__left h5 {
  font-size: 14px;
  font-weight: bold;
  padding-bottom: 33px;
}
.footer__left p {
  font-weight: 400;
  font-size: 14px;
  width: 238px;
  font-family: 'Noto Sans KR', sans-serif;
}
.footer__right {
  display: flex;
  align-items: flex-start;
  gap: 100px;
  width: 800px;
}
.footer__menu p:first-child {
  font-weight: 700;
  position: relative;
  top: -8px;
}
.footer__menu p {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 300;
  font-size: 14px;
}
@media screen and (max-width: 1360px) {
  .footer__right {
    gap: 50px;
    width: 600px;
  }
}
@media screen and (max-width: 960px) {
  .footer__right {
    gap: 20px;
    width: 400px;
  }
  .footer__container {
    margin-left: 40px;
  }
  .footer__left p {
    font-size: 12px;
  }
  .footer__left h5 {
    font-size: 12px;
    padding-bottom: 20px;
  }
  .footer__menu p {
    font-size: 12px;
  }
  .footer__menu p:first-child {
    font-weight: 700;
    position: relative;
    top: -8px;
  }
}
