.hero,
.introduction,
.bestProduct,
.products,
.brandStory,
.new,
.vdHome,
.footer,
.info {
  max-width: 1920px;
}
.hero {
  height: 100%;
  background-color: var(--bn-bg);
}
.introduction {
  height: 100%;
  padding: 138px 0;
  display: flex;
  align-items: center;
  background-image: url(../../assets/bg/introductionBg.jpg);
  background-size: cover;
  background-position: center;
}
.bestProduct {
  max-width: 1920px;
  height: 100%;
  padding-bottom: 200px;
  background-image: url(../../assets/bg/bestProductBg.jpg);
}
.products {
  height: 100%;
}
.brandStory {
  height: 100%;
  padding-bottom: 150px;
  background-color: var(--mid-blue);
  background-image: url('../../assets/bg/brandStory.png');
  background-repeat: no-repeat;
  background-position: 100% 30%;
}
.new {
  height: 100%;
  background-color: var(--sun-yellow);
}
.vdHome {
  height: 100%;
  max-height: 1540px;
  padding-bottom: 270px;
  margin-bottom: 145px;
  background-image: url('../../assets/bg/video.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media screen and (max-width: 1360px) {
  .bestProduct {
    padding-bottom: 100px;
  }
  .brandStory {
    background-position: center;
  }
}
@media screen and (max-width: 960px) {
  .bestProduct {
    padding-bottom: 100px;
    background-position: 100% 25%;
  }
  .bestProduct {
    padding-bottom: 100px;
    background-position: 50% 100%;
  }
}
