.info__container {
  margin: 20px 101px 115px 101px;
  display: flex;
  justify-content: space-between;
}
.info__location {
  width: 70%;
}
.info__location p {
  font-size: 14px;
  color: var(--light-grey);
}

.info__social img {
  margin-left: 23px;
}
@media screen and (max-width: 1360px) {
  .info__location p {
    font-size: 12px;
  }
  .info__social img {
    margin-left: 18px;
  }
}
