.bestProduct__container {
  width: 100%;
}
.bestProduct__products {
  display: flex;
  padding: 35px 155px 0 175px;
  padding-top: 35px;
  width: 100%;
}
@media screen and (max-width: 1800px) {
  .bestProduct__products {
    padding: 35px 115px 0 135px;
  }
}
@media screen and (max-width: 1360px) {
  .bestProduct__products {
    padding: 85px 115px 0 135px;
  }
}
@media screen and (max-width: 960px) {
  .bestProduct__products {
    padding: 15px 55px 0 55px;
  }
}
