.hero__container {
  padding: 120px 0;

  width: 1500px;
  text-align: center;
  margin: 0 auto;
  position: relative;
}
/* TITLE */
.hero__title-text {
  font-size: 52px;
  font-weight: 800;
  font-style: italic;
  color: var(--txt-black);
  text-transform: uppercase;
  text-align: center;
}
.hero__title-title {
  position: relative;
}

.hero__title h4 {
  font-size: 170px;
  line-height: 170px;
  font-weight: 800;
  text-transform: uppercase;
  transform: translateX(-2%);
  -webkit-text-stroke: 4px var(--black-two);
}
.hero__title h4 {
  position: relative;
  color: var(--white);
  margin-top: 35px;
  z-index: 1;
}

.hero__title-title h4 span {
  opacity: 0;
}
.hero__title-title h4::after {
  content: '';
  position: absolute;
  width: 151px;
  height: 150px;
  top: 37%;
  left: 25%;
  background-color: var(--bn-bg);
  z-index: 100;
}
.hero__slide {
  display: flex;
  width: 1140px;
  height: 50px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  position: relative;
  transform: translateX(-2%);
  padding-top: 110px;
  padding-bottom: 45px;
}

.hero__slide-arrow,
.hero__slide-arrow2 {
  position: absolute;
  font-size: 64px;
  color: var(--txt-black);

  -webkit-text-stroke: 4px var(--txt-black);
  font-weight: bold;
  top: 40.5%;
}
.hero__slide-arrow {
  left: 25%;
}
.hero__slide-arrow2 {
  left: 68%;
}
.hero__slide-text {
  font-size: 20px;
  font-weight: 800;
  text-transform: uppercase;
  width: 200px;
  height: 50px;
  display: flex;
  color: var(--txt-black);
  align-items: center;
  -webkit-text-stroke: 1px var(--txt-black);
  letter-spacing: 0.5px;
  line-height: 30px;
  position: relative;
}
.hero__slide-text:nth-child(1),
.hero__slide-text:nth-child(3) {
  justify-content: center;
  border-radius: 50px;
  border: 4px solid var(--txt-black);
}
.hero__slide-text:nth-child(2) {
  transform: translateX(-5%);
}
.hero__slide-text:nth-child(2)::before,
.hero__slide-text:nth-child(2)::after {
  content: '';
  position: absolute;
  width: 170px;
  height: 4px;
  background-color: var(--black-two);
  left: 15px;
}
.hero__slide-text:nth-child(2)::before {
  top: -20px;
}
.hero__slide-text:nth-child(2)::after {
  top: 60px;
}

.hero__container-img span {
  text-transform: uppercase;
}
.hero__title h4 {
  text-shadow: 3px 15px var(--black-two), -3px 15px var(--black-two);
}
@media screen and (max-width: 1600px) {
  .hero__container {
    width: 1300px;
  }
  .hero__title-text {
    font-size: 46px;
  }
  .hero__title h4 {
    font-size: 150px;
    line-height: 160px;

    margin-top: 25px;
  }
  .hero__title-title h4::after {
    width: 133px;
    height: 130px;
    top: 38%;
    left: 24.5%;
  }
  .hero__slide-text {
    font-size: 18px;
  }
  .hero__slide-arrow,
  .hero__slide-arrow2 {
    font-size: 60px;
  }
}
@media screen and (max-width: 1360px) {
  .hero__container {
    width: 950px;
  }
  .hero__title-text {
    font-size: 35px;
  }
  .hero__title h4 {
    font-size: 120px;
    line-height: 130px;
    margin-top: 20px;
  }
  .hero__title-title h4::after {
    width: 105px;
    height: 110px;
    top: 38%;
    left: 22%;
  }
  .hero__slide-text {
    font-size: 16px;
  }
  .hero__slide-arrow,
  .hero__slide-arrow2 {
    font-size: 50px;
  }
  .hero__slide {
    width: 950px;
    height: 40px;
    padding-top: 90px;
    padding-bottom: 35px;
  }
  .hero__slide-text:nth-child(2)::before {
    top: -15px;
    left: 0;
  }
  .hero__slide-text:nth-child(2)::after {
    top: 55px;
    left: 0;
  }
}
@media screen and (max-width: 960px) {
  .hero__container {
    padding: 80px 0;
    width: 750px;
  }
  .hero__title-text {
    font-size: 35px;
  }
  .hero__title h4 {
    font-size: 100px;
    line-height: 110px;
  }
  .hero__slide-text:nth-child(2)::before,
  .hero__slide-text:nth-child(2)::after {
    width: 150px;
  }
  .hero__title-title h4::after {
    width: 90px;
    height: 100px;
    top: 38%;
    left: 20.5%;
  }
  .hero__slide-text {
    font-size: 14px;
  }
  .hero__slide-arrow,
  .hero__slide-arrow2 {
    font-size: 30px;
  }
  .hero__slide {
    width: 750px;
    height: 40px;
    padding-top: 90px;
    padding-bottom: 35px;
  }
  .hero__slide-text:nth-child(2)::before {
    top: -15px;
    left: 0;
  }
  .hero__slide-text:nth-child(2)::after {
    top: 55px;
    left: 0;
  }
  .hero__slide-text:nth-child(2) {
    transform: translateX(0);
  }
  .hero__slide-arrow {
    left: 25%;
    top: 54.5%;
  }
  .hero__slide-arrow2 {
    left: 68%;
    top: 54.5%;
  }
  .hero__slide-text {
    width: 150px;
  }
}
