.product__card {
  width: 20%;
  padding: 0px 16px 60px 0;
  overflow: hidden;
}
.product__card-img {
  width: 100%;
}
.product__card-img img {
  width: 300px;
  height: 100%;
  object-fit: cover;
}
.product__card-content p {
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
}
.product__card-status {
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.product__card-status span {
  font-size: 16px;
}
.product__card-status img {
  padding: 0 5px;
}
@media screen and (max-width: 1800px) {
  .product__card-img img {
    width: 290px;
  }
}
@media screen and (max-width: 1600px) {
  .product__card {
    width: 25%;
    padding: 0px 20px 60px 0;
  }
}
@media screen and (max-width: 1280px) {
  .product__card {
    width: 33%;
  }
  .product__card-img img {
    width: 300px;
  }
}
@media screen and (max-width: 1280px) {
  .product__card {
    width: 33%;
  }
  .product__card-img img {
    width: 230px;
  }
  .product__card-content p {
    font-size: 14px;
  }
  .product__card-status span {
    font-size: 12px;
  }
}
