.new__container {
  position: relative;
}
.new__content {
  position: relative;
  padding-top: 125px;
  padding-bottom: 145px;
  display: flex;
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 1360px) {
  .new__content {
    padding: 80px 0;
  }
  .new__content {
    width: 960px;
    margin: 0 auto;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1360px) {
  .new__content {
    padding: 50px 0;
  }
  .new__content {
    width: 750px;
  }
}
