.newCard {
  width: 20%;
  background-color: var(--bright-orange);
  text-align: center;
}
.newcard__black {
  background-color: var(--black-two);
}
.newCard_brand {
  font-size: 30px;
  font-weight: 800;
  text-transform: uppercase;
  padding: 25px 0;
  color: var(--white);
  border: 2px solid var(--black-two);
  border-bottom: 4px solid var(--black-two);
}
.newCard__brand-img {
  width: 100%;
  height: 400px;
  border: 3px solid var(--black-two);
}
.newCard__brand-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.newCard__issue {
  padding-top: 90px;
  position: relative;
}
.newCard__issue-text {
  font-size: 55px;
  font-weight: 800;
  text-transform: uppercase;
  width: 200px;
  margin: 0 auto;
  line-height: 50px;
}
.newCard__issue img {
  padding-top: 60px;
}
.newCard__issue--arrow {
  font-size: 118px;
  position: absolute;
  top: 73%;
  left: 35%;
}
@media screen and (max-width: 1600px) {
  .newCard {
    width: 25%;
  }
}
@media screen and (max-width: 1360px) {
  .newCard {
    width: 50%;
  }
  .newCard_brand {
    font-size: 24px;
    padding: 15px 0;
  }
}
@media screen and (max-width: 1360px) {
  .newCard_brand {
    font-size: 18px;
    padding: 10px 0;
  }
}
