.introduction__container {
  width: 750px;
  height: 300px;
  margin-left: 175px;
}
.introduction__container h5,
.introduction__container p {
  color: var(--white);
}
.introduction__container h5 {
  font-size: 50px;
  text-transform: uppercase;
  font-weight: 800;
  letter-spacing: 4px;
  margin: 20px 0;
}
.introduction__container p {
  margin: 30px 0;
  font-size: 20px;
  font-weight: 400;
  line-height: 35px;
}
@media screen and (max-width: 1600px) {
  .introduction__container p {
    font-size: 18px;
  }
}
@media screen and (max-width: 1360px) {
  .introduction__container {
    margin: 0 auto;
  }
  .introduction__container h5 {
    font-size: 40px;
  }
}
@media screen and (max-width: 960px) {
  .introduction__container {
    margin: 0 auto;
  }
  .introduction__container h5 {
    font-size: 34px;
  }
  .introduction__container p {
    font-size: 16px;
    width: 500px;
  }
}
